.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.Avatar {
  border-radius: 5%;
  height: 40vmin;
}

.navbar {
  width: 100%;
  padding: 10px;
  position: fixed;
  top: 0;
  left: 0;
  transition: top 0.6s;
  background-color: grey;
}

.navbar-hidden {
  top: -50px;
}